.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /*background-color: rgb(148, 132, 132);*/
  background-color: rgb(20, 20, 20);
  background-color: transparent;
  height: 10vh; /*vh view hight*/
  color: white;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  /* background-color: red; */
}
.nav_black {
  background-color: rgb(20, 20, 20);
}
.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_left ul {
  width: 140%;
  display: flex;
  list-style: none;
  text-align: center;
  align-items: center;
  cursor: pointer;
}
.header_left ul {
  justify-content: space-between;
  -webkit-box-pack: justify;
  display: flex;
}

.header_right ul {
  justify-content: space-between;
  -webkit-box-pack: justify;
  display: flex;
}
.header_left,
ul,
li {
  display: inline-block;
  padding: 14px 0 8px;
}
.header_right,
ul,
li {
  display: inline-block;
  padding: 14px 0 8px;
}
