.footer_outer_container {
  background-color: #111;
  color: grey;
  display: flex;
  justify-content: center;
}
.footer_inner_container {
  width: 60%;
}
.footer_icons {
  width: 12%;
  display: flex;
  justify-content: space-between;
  margin-left: 10px;
  color: #fff;
}
.footer_data {
  display: flex;
  justify-content: space-between;
}
.footer_data ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 12vh;
}
.footer_data ul li {
  cursor: pointer;
}
.service_code {
  display: flex;
  align-items: center;
  justify-content: left;
  border: #fff 1px solid;
  width: 15%;
  margin-top: 80px;
  margin-bottom: 20px;
  cursor: pointer;
}
.copy-write {
  font-size: 12px;
  margin-bottom: 10px;
}
